import React from 'react';
import {IPA} from "@site/src/components";
import {translate} from "@docusaurus/Translate";
import {transliterate} from '@interslavic/utils';

const alphabet = [
  { isv: 'A', ipa: <><IPA></IPA> ~ <IPA>a</IPA></> },
  { isv: 'B', ipa: <IPA>b</IPA> },
  { isv: 'C', ipa: <IPA>ts</IPA> },
  { isv: '', ipa: <><IPA>t</IPA> ~ <IPA>t</IPA></>, pl: 'cz' },
  { isv: 'D', ipa: <IPA>d</IPA> },
  { isv: 'D', ipa: <><IPA>d</IPA> ~ <IPA>d</IPA></>, pl: 'd', sr: '', mk: '' },
  { isv: 'E', ipa: <><IPA></IPA> ~ <IPA>e</IPA></>, ru: '' },
  { isv: '', ipa: <><IPA>j</IPA> ~ <IPA>je</IPA></>, pl: 'ie', sk: 'ie', sl: 'je', ru: '', bg: '' },
  { isv: 'F', ipa: <IPA>f</IPA> },
  { isv: 'G', ipa: <><IPA>g</IPA> ~ <IPA></IPA></>, uk: '', ru: '' },
  { isv: 'H', ipa: <IPA>x</IPA>, pl: 'ch', cz: 'ch', sl: 'ch' },
  { isv: 'I', ipa: <><IPA>i</IPA> ~ <IPA></IPA></>, uk: '', ru: '', bg: '' },
  { isv: 'J', ipa: <IPA>j</IPA>, ru: '', uk: '', bg: '' },
  { isv: 'K', ipa: <IPA>k</IPA> },
  { isv: 'L', ipa: <><IPA>l</IPA> ~ <IPA></IPA></> },
  { isv: 'LJ', ipa: <><IPA>l</IPA> ~ <IPA></IPA></>, pl: 'l', cz: 'l', ru: '', uk: '', bg: '' },
  { isv: 'M', ipa: <IPA>m</IPA> },
  { isv: 'N', ipa: <IPA>n</IPA> },
  { isv: 'NJ', ipa: <><IPA>n</IPA> ~ <IPA></IPA></>, pl: '', cz: '', ru: '', uk: '', bg: '' },
  { isv: 'O', ipa: <><IPA></IPA> ~ <IPA>o</IPA></> },
  { isv: 'P', ipa: <IPA>p</IPA> },
  { isv: 'R', ipa: <IPA>r</IPA> },
  { isv: 'S', ipa: <IPA>s</IPA> },
  { isv: '', ipa: <><IPA></IPA> ~ <IPA></IPA></>, pl: 'sz' },
  { isv: 'T', ipa: <IPA>t</IPA> },
  { isv: 'U', ipa: <IPA>u</IPA> },
  { isv: 'V', ipa: <><IPA>v</IPA> ~ <IPA></IPA></>, pl: 'w' },
  { isv: 'Y', ipa: <><IPA>i</IPA> ~ <IPA></IPA></>, uk: '', ru: '', bg: '' },
  { isv: 'Z', ipa: <IPA>z</IPA> },
  { isv: '', ipa: <><IPA></IPA> ~ <IPA></IPA></>, pl: '' },
];

export default function PronunciationOfTheAlphabet({children}) {
  const rows = React.Children.toArray(
    React.Children.only(children).props.children
  ).filter(item => item.type === 'li');

  return (
    <table>
      <thead>
        <tr>
          <th>{translate({ id: 'com.script.Latin', message: 'Latin' })}</th>
          <th>{translate({ id: 'com.script.Cyrillic', message: 'Cyrillic' })}</th>
          <th>{translate({ id: 'com.script.IPA', message: 'IPA' })}</th>
          <th>{translate({ id: 'com.markdown.Pronunciation', message: 'Pronunciation' })}</th>
        </tr>
      </thead>
      <tbody>
        {alphabet.map((letter, index) => (
          <tr key={index}>
            <td lang="art-Latn-x-interslv">{letter.isv}</td>
            <td lang="art-Cyrl-x-interslv">{transliterate(letter.isv, 'art-Cyrl-x-interslv')}</td>
            <td>{React.cloneElement(letter.ipa)}</td>
            <td>{rows[index].props.children}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
